@font-face {
  font-family: "Lato";
  font-display: swap;
  src: url("../../fonts/Lato-Black.woff2") format("woff2"),url("../../fonts/Lato-Black.woff") format("woff");
  font-style: normal;
  font-weight: 900;
}

@font-face {
  font-family: "Lato";
  font-display: swap;
  src: url("../../fonts/Lato-Bold.woff2") format("woff2"),url("../../fonts/Lato-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
}

@font-face {
  font-family: "Lato";
  font-display: swap;
  src: url("../../fonts/Lato-SemiBold.woff2") format("woff2"),url("../../fonts/Lato-SemiBold.woff") format("woff");
  font-style: normal;
  font-weight: 600;
}

@font-face {
  font-family: "Lato";
  font-display: swap;
  src: url("../../fonts/Lato-Regular.woff2") format("woff2"),url("../../fonts/Lato-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../../fonts/Raleway-Bold.woff2") format("woff2"),url("../../fonts/Raleway-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
}

@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../../fonts/Raleway-Regular.woff2") format("woff2"),url("../../fonts/Raleway-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "Raleway";
  font-display: swap;
  src: url("../../fonts/Raleway-Medium.woff2") format("woff2"),url("../../fonts/Raleway-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
}

.header {
  z-index: 2;
  opacity: 90%;
  background-color: #10101d;
}

.header--fixed {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: .3s all;
  width: 100%;
}

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 50px;
  width: 100%;
  overflow: hidden;
}

.header-logo {
  display: block;
  font-size: 18px;
  font-weight: 800;
  color: #fff;
  text-transform: uppercase;
  font-family: "Raleway",sans-serif;
}

.header-logo__end {
  color: #42a9ed;
}

.header-burger-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 26px;
  height: 16px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 100;
}

.header-burger-menu__line {
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all .3s ease;
}

.header-burger-menu.active .header-burger-menu__line:nth-child(1) {
  transform: translateY(7px) rotate(45deg);
}

.header-burger-menu.active .header-burger-menu__line:nth-child(2) {
  opacity: 0;
}

.header-burger-menu.active .header-burger-menu__line:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg);
}

.nav {
  display: none;
}

.nav__item {
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  color: #fff;
  transition: all ease-in-out .3s;
}

.mobile-menu {
  position: fixed;
  top: 50px;
  right: 0;
  width: 100%;
  height: calc(100vh - 50px);
  background-color: rgba(0,0,0,.9);
  transform: translateX(100%);
  transition: transform .3s ease-in-out;
  z-index: 999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mobile-menu.active {
  transform: translateX(0);
}

.mobile-menu__item {
  margin-bottom: 20px;
}

.mobile-menu__link {
  color: #fff;
  font-size: 24px;
  text-decoration: none;
  transition: color .3s ease;
}

.footer {
  background-color: #3d4050;
  height: auto;
  padding: 16px 0;
  display: flex;
  align-items: center;
}

.footer-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  column-gap: 4px;
  row-gap: 12px;
}

.footer-content__copyright {
  font-size: 13px;
  color: #fff;
  text-align: center;
  position: relative;
  margin-left: 5px;
}

.footer-content__copyright span {
  text-transform: uppercase;
}

.footer-content__separator {
  width: 18px;
  height: 1px;
  background-color: #c3c3c3;
}

.footer-content-wallets {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  position: relative;
}

.footer-content-wallet {
  display: flex;
  align-items: center;
  column-gap: 5px;
}

.footer-content-wallet__icon {
  width: 20px;
  height: 20px;
}

.footer-content-wallet__icon svg {
  width: 100%;
  height: 100%;
  fill: #3e9cdc;
}

.footer-content-wallet__text {
  font-size: 13px;
  color: #c3c3c3;
}

.footer-content-socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

.footer-content-social {
  display: flex;
  align-items: center;
  column-gap: 5px;
}

.footer-content-social__icon {
  width: 20px;
  height: 20px;
}

.footer-content-social__icon svg {
  width: 100%;
  height: 100%;
  fill: #3e9cdc;
}

.footer-content-social__text {
  font-size: 13px;
  color: #fff;
  transition: all ease-in-out .3s;
}

.bildboard {
  height: 560px;
  background-image: url(../img/bildboard.webp);
  background-position-x: 60%;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.bildboard-content {
  width: 100%;
}

.bildboard-content__head {
  font-size: 24px;
  text-transform: uppercase;
}

.bildboard-content__head span {
  color: #3e9cdc;
}

.bildboard-content__head br {
  display: none;
}

.bildboard-content-list {
  margin-top: 36px;
}

.bildboard-content-list__head {
  font-size: 14px;
  font-weight: bold;
}

.bildboard-content-list-ul {
  margin-top: 10px;
}

.bildboard-content-list-ul__item {
  font-size: 14px;
  font-weight: normal;
  padding-left: 16px;
  margin-bottom: 5px;
  position: relative;
}

.bildboard-content-list-ul__item::before {
  content: "-";
  position: absolute;
  left: 5px;
}

.bildboard-content-list-ul__item:last-child {
  margin-bottom: 0;
}

.bildboard-content__btns {
  margin-top: 36px;
  display: flex;
  column-gap: 16px;
}

.bildboard-content__btn:first-child {
  font-size: 11px;
  text-transform: uppercase;
  color: #2a2b30;
  cursor: pointer;
  width: 183px;
  height: 34px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #6f6d7b;
  transition: all ease-in-out .3s;
  color: #fff;
  background-color: #3e9cdc;
  border-color: #fff;
}

.bildboard-content__btn:last-child {
  font-size: 11px;
  text-transform: uppercase;
  color: #2a2b30;
  cursor: pointer;
  width: 183px;
  height: 34px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #6f6d7b;
  transition: all ease-in-out .3s;
}

.order {
  background-color: #272733;
  background-image: url(../img/order_bg.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 36px 0;
}

.order-content__header {
  font-size: 24px;
  font-weight: 900;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
}

.order-content__header span {
  color: #3e9cdc;
}

.order-content__desc {
  margin-top: 16px;
  font-size: 14px;
  color: #bbb;
  text-align: center;
}

.order-content-schema {
  margin-top: 36px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  row-gap: 20px;
}

.order-content-schema__item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 16px;
  position: relative;
}

.order-content-schema__item-text {
  font-size: 16px;
  color: #fff;
  text-align: left;
  width: 100%;
}

.order-content-schema__item-icon {
  width: 55px;
  height: 55px;
  border-radius: 100%;
  background-color: #3e9cdc;
  border: 3px solid #286690;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.order-content-schema__item-icon svg {
  fill: #fff;
  width: 26px;
  height: 26px;
}

.order-content-schema__item-stepper {
  display: none;
  column-gap: 10px;
  position: absolute;
  top: 46px;
  right: -24%;
}

.order-content-schema__item-dot {
  width: 6px;
  height: 6px;
  border-radius: 100%;
  background-color: #c4c4c4;
}

.order-content-form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 16px;
  margin-top: 36px;
}

.order-content-form-block {
  width: 100%;
}

.order-content-form-block--large {
  width: 100%;
}

.order-content-form-block__input {
  font-size: 14px;
  color: #272733;
  background-color: #fff;
  padding: 12px 35px 12px 10px;
  border-radius: 3px;
  width: 100%;
}

.order-content-form-block__input::placeholder {
  font-size: 14px;
  color: #272733;
}

.order-content-form-block-fileload {
  width: 100%;
  height: 34px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
}

.order-content-form-block-fileload__input {
  opacity: 0;
  filter: alpha(opacity=0);
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 34px;
  width: 100%;
  z-index: 2;
  cursor: pointer;
}

.order-content-form-block-fileload__view,
.order-content-form-block-fileload__selected {
  z-index: 1;
  width: 100%;
  height: 34px;
  padding-right: 10px;
  white-space: nowrap;
  text-overflow: ellipsis;
  border: 2px solid #3e9cdc;
  -o-text-overflow: ellipsis;
  border-radius: 3px;
  font-size: 13px;
  color: #272733;
  font-weight: 500;
  background-color: #fff;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 10px;
}

.order-content-form-block-fileload__view svg,
.order-content-form-block-fileload__selected svg {
  width: 17px;
  height: 20px;
  fill: #272733;
}

.order-content-form-block-fileload__view::placeholder,
.order-content-form-block-fileload__selected::placeholder {
  font-size: 14px;
  color: #272733;
}

.order-content-form-block-fileload__selected {
  display: none;
}

.order-content-form-block-fileload__selected.active {
  display: block;
}

.order-content-form-block-fileload__view.hide {
  display: none;
}

.order-content-form-block__label {
  display: flex;
  column-gap: 10px;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  color: #fff;
}

.order-content-form-block__label span {
  font-weight: 500;
  color: rgba(255,255,255,.85);
  white-space: nowrap;
}

.order-content-form-block__range {
  margin-top: 19px;
  -webkit-appearance: none;
  appearance: none;
  background: transparent;
  cursor: pointer;
  width: 100%;
}

.order-content-form-block__range:focus {
  outline: none;
}

.order-content-form-block__range::-webkit-slider-runnable-track {
  background-color: rgba(255,255,255,.7);
  border: 1px solid #fff;
  border-radius: .5rem;
  height: 6px;
}

.order-content-form-block__range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  margin-top: -8px;
  background-color: #42a9ed;
  border-radius: 100%;
  height: 18px;
  width: 18px;
}

.order-content-form-block__range:focus::-webkit-slider-thumb {
  outline: 3px solid #286690;
  outline-offset: 0;
}

.order-content-form-block__range::-moz-range-track {
  background-color: rgba(255,255,255,.7);
  border: 1px solid #fff;
  border-radius: .5rem;
  height: 6px;
}

.order-content-form-block__range::-moz-range-thumb {
  background-color: #42a9ed;
  border: none;
  border-radius: 100%;
  height: 18px;
  width: 18px;
}

.order-content-form-block__range:focus::-moz-range-thumb {
  outline: 3px solid #286690;
  outline-offset: 0;
}

.order-content-form__btns {
  width: 100%;
  text-align: center;
}

.order-content-form__btn {
  font-size: 11px;
  text-transform: uppercase;
  color: #2a2b30;
  cursor: pointer;
  width: 183px;
  height: 34px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #6f6d7b;
  transition: all ease-in-out .3s;
  color: #fff;
  background-color: #3e9cdc;
  border-color: #fff;
  width: 100%;
  height: 34px;
}

.select {
  position: relative;
  z-index: 1;
}

.select__btn {
  position: relative;
  display: block;
  border-radius: 3px;
  background-color: #fff;
  text-decoration: none;
  width: 100%;
  padding: 12px 35px 12px 10px;
  color: #272733;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 14px;
}

.select__btn.active {
  border-radius: 3px 3px 0 0;
  border-bottom: none;
}

.select__btn.active::after {
  transform: translateY(-50%) scale(-1);
}

.select__btn::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 10px;
  width: 20px;
  height: 20px;
  background-image: url(../img/arrow.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transform: translateY(-50%);
  transition: all ease-in-out .3s;
}

.select__drop {
  margin: 0;
  padding: 0;
  width: 100%;
  max-height: 170px;
  border-radius: 0 0 3px 3px;
  border-top: none;
  position: absolute !important;
  background: #fff;
  height: 0;
  transition: all ease-in-out .3s;
}

.select__drop li {
  list-style: none;
  cursor: pointer;
  display: block;
  padding-left: 10px;
}

.select__drop li a {
  display: block;
  width: 100%;
  font-size: 14px;
  line-height: 180%;
  color: #272733;
  transition: all ease-in-out .3s;
}

.select__drop li.active a {
  color: #42a9ed;
}

.select__drop.active {
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
dd,
dt {
  margin: 0;
}

body {
  margin: 0;
  font-family: "Lato",sans-serif;
  font-size: 16px;
  color: #000;
  height: 100%;
  min-height: 100vh;
  background: #fff;
  width: 100%;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 0 auto;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
  color: #000;
  transition: .3s ease-in-out;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

button {
  padding: 0;
  border-style: solid;
  border: none;
}

svg {
  display: block;
  transition: .3s ease-in-out;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

input,
textarea {
  outline: none;
}

textarea {
  resize: none;
}

p {
  margin: 0;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.scrollbar-track-y,
.scrollbar-thumb {
  width: 7px !important;
  border-radius: 3px !important;
}

.scrollbar-track-y {
  background-color: #3d4050 !important;
}

.scrollbar-thumb {
  background-color: #3e9cdc !important;
}

@media screen and (min-width: 500px) {
  .footer-content__copyright {
    max-width: 33%;
    text-align: left;
  }

  .footer-content__separator {
    width: 1px;
    height: 18px;
  }

  .footer-content {
    flex-direction: row;
    row-gap: 0;
  }
}

@media screen and (min-width: 768px) {
  .header-wrapper {
    height: 100px;
  }

  .header-logo {
    font-size: 28px;
  }

  .header-burger-menu {
    display: none;
  }

  .nav {
    display: flex;
    column-gap: 40px;
  }

  .bildboard-content__head br {
    display: block;
  }

  .bildboard-content__head {
    font-size: 48px;
  }

  .bildboard-content-list__head {
    font-size: 16px;
  }

  .bildboard-content-list-ul__item {
    font-size: 18px;
    padding-left: 25px;
  }

  .bildboard-content-list {
    margin-top: 90px;
  }

  .bildboard-content__btns {
    margin-top: 80px;
    column-gap: 30px;
  }

  .bildboard-content__btn:first-child {
    font-size: 13px;
    height: 43px;
  }

  .bildboard-content__btn:last-child {
    font-size: 13px;
    height: 43px;
  }

  .bildboard-content {
    max-width: 780px;
  }

  .bildboard {
    height: 850px;
    background-position: center;
  }

  .order-content__header {
    font-size: 28px;
  }

  .order-content__desc {
    font-size: 16px;
  }

  .order-content-form-block__input::placeholder {
    font-size: 16px;
  }

  .order-content-form-block__input {
    font-size: 16px;
  }

  .order-content-form-block-fileload__input {
    height: 48px;
  }

  .order-content-form-block-fileload__view::placeholder,
  .order-content-form-block-fileload__selected::placeholder {
    font-size: 16px;
  }

  .order-content-form-block-fileload__view,
  .order-content-form-block-fileload__selected {
    font-size: 15px;
    height: 48px;
  }

  .order-content-form-block-fileload {
    height: 48px;
  }

  .order-content-form-block__label {
    font-size: 16px;
  }

  .order-content-form-block__range::-webkit-slider-thumb {
    height: 23px;
    width: 23px;
    margin-top: -10px;
  }

  .order-content-form-block__range::-moz-range-thumb {
    height: 23px;
    width: 23px;
    margin-top: -10px;
  }

  .order-content-form__btns {
    margin-top: 23px;
  }

  .order-content-form__btn {
    font-size: 13px;
    height: 43px;
  }

  .order-content-form__btn {
    width: 370px;
    height: 48px;
  }

  .order {
    padding: 60px;
  }

  .select__btn {
    font-size: 16px;
  }

  .select__drop li a {
    font-size: 16px;
    line-height: 34px;
  }

  .container {
    padding: 0 30px;
  }
}

@media screen and (min-width: 920px) {
  .nav__item {
    font-size: 13px;
    margin-top: 4px;
  }

  .nav {
    column-gap: 60px;
  }

  .bildboard-content-list__head {
    font-size: 20px;
  }

  .bildboard-content-list-ul__item {
    font-size: 20px;
  }

  .order-content__header {
    letter-spacing: .5px;
    font-size: 36px;
  }

  .order-content__desc {
    font-size: 18px;
    margin-top: 30px;
  }

  .order-content-schema__item-text {
    text-align: center;
  }

  .order-content-schema__item-icon svg {
    width: 55px;
    height: 55px;
  }

  .order-content-schema__item-icon {
    width: 100px;
    height: 100px;
    border: 5px solid #286690;
  }

  .order-content-schema__item-stepper {
    display: flex;
  }

  .order-content-schema__item {
    flex-direction: column;
    width: 209px;
    gap: 20px;
  }

  .order-content-schema {
    flex-direction: row;
    row-gap: 0;
    margin-top: 80px;
  }

  .order-content-form-block--large {
    width: calc(66.66% - 10px) !important;
  }

  .order-content-form-block__input::placeholder {
    font-size: 18px;
  }

  .order-content-form-block__input {
    font-size: 18px;
  }

  .order-content-form-block-fileload__view::placeholder,
  .order-content-form-block-fileload__selected::placeholder {
    font-size: 18px;
  }

  .order-content-form-block__label {
    font-size: 18px;
  }

  .order-content-form-block {
    width: calc(33.33% - 20px);
  }

  .order-content-form {
    margin-top: 85px;
    gap: 30px;
  }

  .order {
    padding: 118px 0 150px;
  }

  .select__btn {
    font-size: 18px;
  }

  .select__drop li a {
    font-size: 18px;
  }

  .container {
    max-width: 1320px;
    padding: 0 76px;
  }
}

@media screen and (min-width: 1100px) {
